:host {
  --tp-background-left: var(--tp-color-brand-1);
  --tp-background-right: var(--tp-color-neutral-1);
  --tp-background-circle: var(--tp-color-brand-3);
  --tp-border-color: var(--tp-color-brand-4);
  --tp-color-text: var(--tp-color-neutral-10);

  cursor: pointer;
}

:host ion-radio-group {
  width: 100%;
}

.tp-theme {
  display: flex;

  &:not(:last-child) {
    margin-bottom: 16px;
  }

  .tp-theme__left {
    padding: 16px 8px;
    width: 54px;
    display: flex;
    align-items: center;
    justify-content: center;
    background: var(--tp-background-left);
    border: 2px solid var(--tp-border-color);
    border-top-left-radius: 8px;
    border-bottom-left-radius: 8px;
  }

  .tp-theme__circle {
    display: block;
    width: 32px;
    height: 32px;
    border-radius: 50%;
    background: var(--tp-background-circle);
  }

  .tp-theme__right {
    display: flex;
    width: 100%;
    flex-direction: column;
    padding: 16px;
    background: var(--tp-background-right);
    border-top-right-radius: 8px;
    border-bottom-right-radius: 8px;
    border-top: 2px solid var(--tp-border-color);
    border-right: 2px solid var(--tp-border-color);
    border-bottom: 2px solid var(--tp-border-color);
  }

  .tp-theme__bar {
    display: block;
    width: 100%;
    height: 8px;
    border-radius: 4px;
    background: var(--tp-color-neutral-6);
    margin: 4px 0;

    &--small {
      width: 80%;
    }
  }

  .tp-theme__wrapper {
    margin-top: 8px;
    display: flex;
    align-items: center;
  }

  .tp-theme__name {
    font-size: 16px;
    line-height: 100%;
    font-weight: 400;
    margin-left: 16px;
    color: var(--tp-color-text);
  }
}